//封装http请求
import axios from 'axios'
//GET 这个方法formObj可以默认为空
export function httpGet(url,cb,formObj={}){
  // axios.get('/api'+url,formObj)
  axios.get(url,formObj)
  .then(function(res){
    cb(res)
  })
  .catch(function(err){
    alert(err)
  })
}
export function httpPost(url,formObj,cb){
  // axios.get('/api'+url,formObj)

  axios.post(url,formObj)
  .then(function(res){
    cb(res)
  })
  .catch(function(err){
    alert(err)
  })
}

export function httpBinaryPost(url,formObj,cf){
  // axios.get('/api'+url,formObj)

  axios.post(url,formObj).then(function(res){
    cf(res);
  }).catch(function(err){
    alert(err)
  });
}



